.as-products-single .wc-block-grid ul {
  gap: 25px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  li.wc-block-grid__product {
    width: 100%;
    max-width: 100%;
    border: none;
    padding: 0;
    .wc-block-components-product-image {
      img {
        width: 100%;
        max-width: 100%;
      }
    }
    .wc-block-components-product-price {
      display: none;
    }
    a.wc-block-components-product-name {
      font-size: $font-size-medium;
    }
    .wc-block-components-product-button {
      display: none;
    }
  }
  @include breakpoint-max(medium) {
    gap: 30px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
.as-single-product-page {
  .wp-block-group.wp-container-content-2.is-vertical.is-content-justification-center.is-layout-flex.wp-container-core-group-is-layout-4.wp-block-group-is-layout-flex {
    max-height: 65vh !important;

    overflow: hidden;
    .wc-block-product-gallery-large-image-next-previous--inside-image {
      bottom: 37%;
    }
    .wc-block-product-gallery-large-image__image-element {
      width: 100%;
      img {
        max-width: 100%;
        width: 100%;
      }
    }
  }
}
.single_add_to_cart_button {
  background-color: $secondary;
  color: #fff;
  border: none;
  border-radius: 0;
  padding: 10px 30px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.wc-block-add-to-cart-form .quantity .qty {
  padding: 10px 12px;
  border-radius: 0px;
  border: 1px solid #ccc;
  width: 50px;
  text-align: center;
}
